import HomeElement from './Home.html';
import './Home.scss'

export let categoryType;
export class Home {
  constructor() {
    this.count = 0;
  }

  async render() {
    return HomeElement;
  }

  async after_render() {
    const typeCategories = document.querySelectorAll('.quitz__type-link');
    typeCategories.forEach(el => {
      el.addEventListener('click', () => {
        categoryType = el.dataset.value;
      })
    })
  }

}