import Questions from './Questions-pictures.html';
import './Questions-pictures.scss';
import {
  idCategory
} from '../Categories/Categories';
import {
  blockQuestionsByName
} from '../Categories/Categories';
import { shuffle } from '../Questions/Questions';


const innerHtmlQuestionByPictures = (arr, block) => {
  let arrRandom = shuffle(arr);
  block.removeChild(block.firstChild);
  let newBlock = document.createElement('div');
  newBlock.classList.add('answer__block-pictures');
  block.append(newBlock);
  arrRandom.forEach((el) => {
    let subBlock = document.createElement('button');
    subBlock.classList.add('answer-pictures');
    subBlock.style.background = `url(../../data/img/${el}.jpg) center no-repeat`;
    subBlock.style.backgroundSize = 'contain';
    subBlock.setAttribute('value', el);
    newBlock.append(subBlock);
  });
};

let countQuestions = 0;

export class QuestionsPictures {
  constructor() {}

  async render() {
    return Questions;
  }

  async after_render() {
    const answerBlock = document.querySelector('.answer__block-pictures-wrap');
    const quizQuestion = document.querySelector('.quiz__question');
    
    let answer = blockQuestionsByName[idCategory].answer[countQuestions];
    let author = answer.author;
    let img = answer.imageNum;
    let namePictures = answer.name;
    let year = answer.year;
    console.log(blockQuestionsByName[idCategory])
    quizQuestion.innerHTML = `Какую картину написал ${author}?`;
    innerHtmlQuestionByPictures(blockQuestionsByName[idCategory].questionsByPictures[countQuestions], answerBlock);

    const answerPictures = document.querySelectorAll('.answer-pictures');
    const modalAuthor = document.querySelector('.modal__author');
    const modalTextName = document.querySelector('.modal__text-name');
    const modalTextAuthor = document.querySelector('.modal__text-year');
    const congratulationsBlock = document.querySelector('.modal__congratulations');
    const congratulationsResult = document.querySelector('.congratulations__result')
    modalTextName.innerHTML = namePictures;
    modalTextAuthor.innerHTML = `${author} ${year}`;
    
    answerPictures.forEach(item => {
      item.addEventListener('click', () => {
        console.log(item.value);
        if (item.value === img) {
          item.classList.add('answer-pictures-true');
          correctAnswer++;
        } else {
          item.classList.add('answer-pictures-false');
          incorrectAnswer++;
          answerPictures.forEach(el => {
            if (el.value === author) el.classList.add('answer-pictures-true');
          })
        }
        modalAuthor.classList.add('modal__activ');

        if (countQuestions < (blockQuestionsByName[idCategory].questionsByPictures.length - 1)) {
          countQuestions++;
        } else {
          congratulationsBlock.classList.add('modal__congratulations__activ');
          congratulationsResult.innerHTML = `${correctAnswer}/${countQuestions + 1}`;
          countQuestions = 0;
          correctAnswer = 0;
          incorrectAnswer = 0;
        }
      });
    })
  }
}
