import QuestionsBlock from './Questions.html';
import './Questions.scss';
import {
  idCategory
} from '../Categories/Categories';
import {
  blockQuestionsByAuthor
} from '../Categories/Categories';

export const innerHtmlQuestion = (arr, block) => {
  let arrRandom = shuffle(arr);
  block.removeChild(block.firstChild);
  let newBlock = document.createElement('div');
  newBlock.classList.add('quiz__answer-block');
  block.append(newBlock);
  arrRandom.forEach((el, id) => {
    let subBlock = document.createElement('button');
    subBlock.classList.add('btn-answer');
    subBlock.innerHTML = el;
    subBlock.setAttribute('value', el);
    newBlock.append(subBlock);
  });
};
export const shuffle = (array) => {
  for (let i = array.length - 1; i > 0; i--) {
    let j = Math.floor(Math.random() * (i + 1));
    [array[i], array[j]] = [array[j], array[i]];
  }
  return array;
};

export let countQuestions = 0;

let correctAnswer = 0;
let incorrectAnswer = 0;

export class Questions {
  constructor() {}

  async render() {
    return QuestionsBlock;
  }

  async after_render() {
    const imgQuestions = document.querySelector('.img__quistion');
    const quizAnswer = document.querySelector('.quiz__answer-wrap');
    let answer = blockQuestionsByAuthor[idCategory].answer[countQuestions];
    let author = answer.author;
    let img = answer.imageNum;
    let namePictures = answer.name;
    let year = answer.year;
    imgQuestions.style.background = `url(../../data/img/${img}.jpg) center no-repeat`;
    imgQuestions.style.backgroundSize = 'contain';
    innerHtmlQuestion(blockQuestionsByAuthor[idCategory].questionsByAutor[countQuestions], quizAnswer);
    const btnAnswer = document.querySelectorAll('.btn-answer');
    const modalAuthor = document.querySelector('.modal__author');
    const modalTextName = document.querySelector('.modal__text-name');
    const modalTextAuthor = document.querySelector('.modal__text-year');
    const congratulationsBlock = document.querySelector('.modal__congratulations');
    const congratulationsResult = document.querySelector('.congratulations__result')
    modalTextName.innerHTML = namePictures;
    modalTextAuthor.innerHTML = `${author} ${year}`;
    btnAnswer.forEach(item => {
      item.addEventListener('click', () => {
        if (item.value === author) {
          item.classList.add('question-true');
          correctAnswer++;
        } else {
          item.classList.add('question-false');
          incorrectAnswer++;
          btnAnswer.forEach(el => {
            if (el.value === author) el.classList.add('question-true');
          })
        }
        modalAuthor.classList.add('modal__activ');

        if (countQuestions < (blockQuestionsByAuthor[idCategory].questionsByAutor.length - 1)) {
          countQuestions++;
        } else {
          congratulationsBlock.classList.add('modal__congratulations__activ');
          congratulationsResult.innerHTML = `${correctAnswer}/${countQuestions + 1}`;
          countQuestions = 0;
          correctAnswer = 0;
          incorrectAnswer = 0;
        }
      });
    })

  }
}