import CongratulationsElnent from './Congratulations.html';
import './Congratulations.scss';


export class Congratulations {
  constructor() {}

  async render() {
    return CongratulationsElnent;
  }

  async after_render() {
    const congratulationsBtn = document.querySelectorAll('.congratulations__btn');
    const congratulationsBlock = document.querySelector('.modal__congratulations');
    congratulationsBtn.forEach(el => {
      el.addEventListener('click', () => {
        congratulationsBlock.classList.remove('modal__congratulations__activ')
      })
    })
  };
}