'use strict';

import './styles/style.scss';

import { Home } from './pages/Home';
import { Categories } from './pages/Categories';
import { Settings } from './pages/Settings';
import { Error404 } from './pages/Error404';
import { Questions } from './pages/Questions';
import { QuestionsPictures } from './pages/Questions-pictures';

import { Header } from './components/Header';
import { Footer } from './components/Footer';
import { Modal } from './components/Modal';
import { Congratulations } from './components/Congratulations';

import { Utils } from './utils/Utils';

const homeInstance = new Home();
const settingsSettings = new Settings();
const categoriesInstance = new Categories();
const questionsInstance = new Questions();
const questionsPicturesInstance = new QuestionsPictures();
const error404Instance = new Error404();

const headerInstance = new Header();
const footerInstance = new Footer();
const modalInstance = new Modal();
const congratulationsInstance = new Congratulations();

const routes = {
  '/': homeInstance,
  '/settings': settingsSettings,
  '/categories': categoriesInstance,
  '/questions-artist': questionsInstance,
  '/questions-pictures': questionsPicturesInstance,
};

const router = async () => {
  const header = null || document.getElementById('header_container');
  const content = null || document.getElementById('page_container');
  const footer = null || document.getElementById('footer_container');
  const modal = null || document.getElementById('modal_container');
  const congratulations = null || document.getElementById('congratulations_container');

  header.innerHTML = await headerInstance.render();
  await headerInstance.after_render();

  footer.innerHTML = await footerInstance.render();
  await footerInstance.after_render();

  modal.innerHTML = await modalInstance.render();
  await modalInstance.after_render();

  congratulations.innerHTML = await congratulationsInstance.render();
  await congratulationsInstance.after_render();

  const request = Utils.parseRequestURL();

  const parsedURL =
    (request.resource ? `/${request.resource}` : '/') +
    (request.id ? '/:id' : '') +
    (request.verb ? `/${request.verb}` : '');

  const page = routes[parsedURL] ? routes[parsedURL] : error404Instance;

  content.innerHTML = await page.render();

  await page.after_render();
};

window.addEventListener('hashchange', router);
window.addEventListener('load', router);
