import CategoriesElement from './Categories.html';
import './Categories.scss';
import { images } from '@/data/images';

import { categoryType } from '../Home/Home';

const questionsByAuthor = [];
const questionsByName = [];

images.forEach((el, id) => {
  if (id % 2 === 0 && id < 240) {
    questionsByAuthor.push({
      ...el,
      type: 'author',
    });
  }
  if (id % 2 !== 0) {
    questionsByName.push({
      ...el,
      type: 'pictures',
    });
  }
});




const splitQuestion = (arr, chunk) => {
  const resArr = [];
  for (let i = 0; i < arr.length; i += chunk) {
    resArr.push(arr.slice(i, i + chunk));
  }
  return resArr;
};

export const randomElem = (min, max) => {
  return Math.floor(Math.random() * (max - min + 1)) + min;
};

const categoriesByAuthor = splitQuestion(questionsByAuthor, 10);
const categoriesByName = splitQuestion(questionsByName, 10);


const uniqAnswersByAuthor = [...new Set(questionsByAuthor.map((item) => item.author))];
const uniqAnswersByName = [...new Set(questionsByName.map((item) => item.imageNum))];



const innerHtml = (arr, block) => {
  arr.forEach((el, id) => {
    let item = 3;
    let subBlock = document.createElement('div');
    subBlock.classList.add('cards__category-item');
    subBlock.classList.add(`cards__category-${id}`);
    subBlock.innerHTML = `<a class="navbar-item" href="/#/${categoryType}">
                            <div class="category__progress">
                              <p class="name__category">Category ${id + 1} </p>
                              <p class="category__progress">5/10</p>
                            </div>
                            <img src="../../data/img/${
                              el[item].imageNum
                            }.jpg" alt="category 1" class="category__img">
                          </a>`;
    block.append(subBlock);
  });
};

const quistion = (arr, uniq, key) => {
  return arr.map((el, id) => {
    let resArr = [];
    while (resArr.length < 3) {
      let item = randomElem(0, uniq.length - 1);
      if (uniq[item] !== el[key]) resArr.push(uniq[item]);
    }
    resArr.push(el[key]);
    return resArr;
  });
};

export const blockQuestionsByAuthor = categoriesByAuthor.map((item) => {
  let questionsByAutor = quistion(item, uniqAnswersByAuthor, 'author');
  return { answer: item, questionsByAutor: questionsByAutor };
});
export const blockQuestionsByName = categoriesByName.map((item) => {
  let imageNum = quistion(item, uniqAnswersByName, 'imageNum');
  return { answer: item, questionsByPictures: imageNum };
});

export let idCategory = '';

export class Categories {
  constructor() {}
  async render() {
    return CategoriesElement;
  }

  async after_render() {
    const classCategory = document.querySelector('.cards__category');
    categoryType === 'questions-artist'
      ? innerHtml(categoriesByAuthor, classCategory)
      : innerHtml(categoriesByName, classCategory);
    const cardsCategory = document.querySelectorAll('.cards__category-item');
    cardsCategory.forEach((el, id) => {
      el.addEventListener('click', () => {
        idCategory = id;
      });
    });
  }
}
